@import url(https://fonts.googleapis.com/css2?family=Fredericka+the+Great&family=Noto+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

#end-message {
  height: 2rem;
  font-size: x-large;
  font-weight: 700;
}

body {
  font-family: 'Noto Sans', sans-serif;
  background: linear-gradient(5deg, #587285, #6ec2a6, #f49a87);
  min-height: 100vh;
}

h1,
span {
  font-family: 'Fredericka the Great', cursive;
  text-shadow: 2px 2px 2px #e7daa9, 0 0 5px #e6a546;
}

span {
  font-size: x-large;
  font-weight: 700;
}

#wrapper-cards-container {
  display: flex;
  justify-content: center;
}

img {
  width: 306px;
  margin: 3px;
}

@media only screen and (min-width: 1000px) {
  body {
    font-size: large;
  }
  h1 {
    font-size: 3rem;
  }
  img {
    width: 153px;
    margin: 3px;
  }
  #cards-container {
    width: 1000px;
  }
}

@media only screen and (min-width: 600px) and (max-width: 999px) {
  body {
    font-size: large;
  }
  h1 {
    font-size: 2rem;
  }
  img {
    width: 115px;
    margin: 3px;
  }
  #cards-container {
    width: 600px;
  }
}

@media only screen and (min-width: 500px) and (max-width: 599px) {
  body {
    font-size: large;
  }
  h1 {
    font-size: 2rem;
  }
  img {
    width: 115px;
    margin: 3px;
  }
}

@media only screen and (max-width: 499px) {
  img {
    width: 100px;
    margin: 3px;
  }
}

